.footer-container {
  height: 50vh;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.footer-column {
  display: flex;
  flex-direction: column;
  /* padding: 10%; */
}
.linkContainer {
  display: flex;
  flex-direction: column;
  align-items: baseline;
  padding: 20px;
}
.footerLink {
  display: flex;
  flex-direction: column;
  text-decoration: none;
  padding: 10px;
  justify-content: center;
  align-content: center;
  color: black;
}
.reference {
  color: rgb(211, 211, 211);
  text-decoration: none;
  margin-bottom: 20px;
  font-size: 1.2rem;
  font-family: cursive;
  margin: 20px;
}

.icon {
  height: 30px;
  width: 30px;
}

@media screen and (min-width: 500px) {
  .iconContainer {
    margin-top: 50px;
    display: grid;
    /* grid-template: 80px 80px 80px / 1fr 1fr; */
    justify-items: center;
  }
  .icon {
    height: 60px;
    width: 60px;
  }
}
