.App {
  text-align: center;
  overflow-y: hidden;
  overflow-x: hidden;
  width: 100vw;
}
::-webkit-scrollbar {
  display: none;
}

.navbar {
  width: 100vw;
  position: absolute;
  justify-content: center;
  display: flex;
  background-color: rgb(0, 0, 0, 0.5);
  z-index: 1;
  width: 100vw;
  grid-template-columns: auto 1fr;
  align-items: center;
  font-family: parent;
  color: white;
}

#join {
  font-family: cursive;
  /* margin-left: 10%; */
  right: 10;
  justify-content: center;
  /* border-color: red; */
  border-style: solid;
  border-color: orangered;
  width: 200px;
  height: 30px;
  border-radius: 20px;
}

#btn {
  display: block;
  /* margin: 150px auto 0; */
  font-size: 25px;
  font-weight: lighter;
  color: #333;
  /* padding: 15px 50px; */

  background: #ffd17b;
  outline: none;
  border: none;
  cursor: pointer;
  border-radius: 20px;
  position: absolute;
  right: 30px;
  box-shadow: 0 6px #efa424;
}
#btn:hover {
  box-shadow: 0 4px #efa424;
  /* top: 1px; */
  color: #ff5733;
}
#btn:active {
  box-shadow: 0 0 #efa424;
  /* top: 6px; */
}
/* Style for the logo */
.logo {
  font-size: 7vw;
  min-height: 50px;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  font-weight: 900;
  background-image: linear-gradient(45deg, #ffa500, #ff4500);
  font-family: parent;
  padding-top: 5px;
}

/* Styles for the navigation links */
.links {
  display: none;
  /* justify-content: center;  */
  /* gap: 20px; */
}

.link {
  text-decoration: none;
  color: white;
  font-size: 23px;
  transition: color 0.3s;
  padding: 0 20px;
  justify-content: center;
  font-family: parent;
}

.link:hover {
  color: #ff5733;
}

/* Responsive Navbar */
.responsive_nav {
  flex-direction: column;
  align-items: flex-start;
}
.menu-button {
  background: none;
  border: none;
  font-size: 24px;
  color: white;
  cursor: pointer;
  padding: 0 10px;
  z-index: 2;
  position: absolute;
  top: 10px;
  left: 10px;
}

.navbar.open {
  display: block;
  position: absolute;
  /* top: 60px; */
  background-color: rgb(0, 0, 0, 0.5);
  width: 100%;
}
.navbar.open .links {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.navbar.open .link {
  padding: 7px;
}
.navbar.open #btn {
  position: relative;
  margin-left: 35px;
}
@media (min-width: 768px) {
  .menu-button {
    display: none;
  }
  .links {
    display: flex;
    justify-content: center; /* Aligner les liens à droite */
    gap: 20px; /* Espacement entre les liens */
  }

  .logo {
    font-size: 40px; /* Taille de la police pour le logo */
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    font-weight: 900;
    background-image: linear-gradient(45deg, #ffa500, #ff4500);
    font-family: parent;
    padding-left: 20px;
  }

  .navbar {
    position: absolute;
    display: grid;
    background-color: rgb(0, 0, 0, 0.5);
    z-index: 1;
    width: 100vw;
    grid-template-columns: auto 1fr;
    font-family: parent;
    color: white;
    padding-left: 20px;
  }
  /* Masquer la barre de navigation principale par défaut sur les petits écrans */

  /* Afficher la barre de navigation lorsque le bouton du menu est cliqué */
}
