.lines {
  padding-top: 10px;
  color: white;
  font-weight: 400;
  font-size: 4vh;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

@font-face {
  font-family: parent;
  src: url("../assets/fonts/Parent.ttf");
}

@media screen and (min-width: 770px) {
  .iconContainer {
    margin-top: 50px;
    display: grid;
    grid-template: 80px 80px 80px / 1fr 1fr;
    justify-items: center;
  }
  .icon {
    height: 60px;
    width: 60px;
  }
  .socialContainer {
    position: absolute;
    right: 10px;
    top: 70px;
    z-index: 1;
  }
  .video {
    display: block;
  }
  .gif {
    display: none;
  }
  .lines {
    padding-top: 50px;
    color: white;
    font-weight: 600;
    font-size: 7vh;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
  }
  .imageGallery1 {
    display: none;
  }
}
